<template>
  <div class="post">
    <div class="layer layer-1">
      <h1 class="post-title">{{ this.postData.title }}</h1>
    </div>
    <div class="layer layer-2">
      <div class="post-content" v-html=postContent></div>
    </div>
  </div>
</template>

<script>
import SimpleMDE from 'simplemde';

export default {
  name: 'post',

  beforeMount() {
  
    this.populatePost();
  },
  watch: {
    '$route'() {
      
      this.populatePost();
    }
  },

  data: () => ({
    
    postContent: '',
    postData: {
      markdownContent: '',
      title: 'Loading...',
      postViews: 0,
      postAppreciations: 0,
      postKeywords: [],
      
      owner: {
        profileAsset: {
          path: ''
        },
        userName: '',
        name: '',
        description: '',
        privilegeStatus: ''
      }
    }

  }),


  methods: {
    populatePost() {
      
      this.$store.dispatch( 'populatePost', {
        userName: this.$route.params.username,
        postId: this.$route.params.postid

      }).then( postData => {
        
        this.postData = postData;
        this.postContent = SimpleMDE.prototype.markdown( this.postData.markdownContent );

      })
    }
  }
}
</script>

<style scoped>
.layer {
  margin-top: 20px;
}
.layer-1 {
  margin-top: 50px;
}
.post-title {
  text-align: center;
  color: #eee;
  margin: auto;
  font-size: 34px;
}
.post-content {
  padding: 18px;
  width: 612px;
  margin: auto;
  background-color: #f8f8f8;
}
</style>



